import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { getInterpolatedColours, getClosestColour, colourToString } from './utils/utils';
import Vector from './utils/vector';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const colourArr = getInterpolatedColours([
  { r: 241, g: 100, b: 92},
  { r: 148, g: 147, b: 242},
  { r: 93, g: 213, b: 247},
  { r: 241, g: 100, b: 92},
], 250);

const header = new Vector({
  x: WIDTH * (Math.round(Math.random() * 10) / 10),
  y: HEIGHT * (Math.round(Math.random() * 10) / 10),
});
const headerSpeed = new Vector().setAngle(0).setMag(3);
let lastAngle = 0;
let boxSize = 40;
let tolerance = boxSize;

function getRandomAngle() {
  let r = (Math.ceil(Math.random() * 4) / 4);
  let _angle = (r * (Math.PI * 2)) + (Math.PI / 4);

  if (_angle == lastAngle || _angle == Math.PI) {
    return getRandomAngle();
  }

  return _angle;
}

let totalDots = 250;
let onTop = false;
let dots = new Array(totalDots).fill(0).map(() => {
  return {
    position: header.clone(),
    color: getClosestColour(0, colourArr),
    top: onTop
  };
});

function cascadeDots() {
  // if (dots.length < totalDots) {
  //   dots.push({...dots[dots.length - 1]});
  // }

  for (let i = dots.length - 1; i >= 0; i--) {
    if (i == 0) {
      dots[i].position.set(header.x, header.y);
      let t = (headerSpeed.getAngle() + Math.PI) / (Math.PI * 2);
      dots[i].color = getClosestColour(t, colourArr);
      dots[i].top = onTop;
      dots[i].size = currSize;
    } else {
      dots[i] = {
        ...dots[i - 1],
        position: dots[i - 1].position.clone()
      };
    }
  }
}

function drawBoxes(top) {
  for (let i = dots.length - 1; i >= 0; i--) {
    if (dots[i].top == top) {
      ctx.beginPath();
      ctx.strokeStyle = colourToString(dots[i].color);
      // ctx.rect(dots[i].position.x, dots[i].position.y, boxSize, boxSize);
      ctx.arc(
        dots[i].position.x,
        dots[i].position.y,
        // ((boxSize / 2) * dots[i].size) + (boxSize / 2),
        boxSize,
        0,
        Math.PI * 2
      );
      ctx.stroke();
    }
  }
}

function inSafeSpot() {
  return header.x > WIDTH * 0.3 && header.x < WIDTH * 0.7 &&
    header.y > HEIGHT * 0.3 && header.y < HEIGHT * 0.7;
}

let txt = `&`;
let fontSize = 320;
ctx.font = `600 ${fontSize}px Helvetica`;
let size = ctx.measureText(txt);
let currSize = 1;

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  header.add(headerSpeed);
  cascadeDots();

  currSize = (Math.sin(((frame % 240) / 240) * Math.PI * 2) + 1) / 2;

  if (frame % 120 === 0 && frame > 0 && !inSafeSpot()) {
    headerSpeed.rotate(getRandomAngle());
    onTop = !onTop;
  }

  if (header.x > width + tolerance) {
    header.x = -tolerance;
  }
  if (header.y > height + tolerance) {
    header.y = -tolerance;
  }
  if (header.x < -tolerance) {
    header.x = width + tolerance;
  }
  if (header.y < -tolerance) {
    header.y = height + tolerance;
  }

  drawBoxes(false);

  ctx.fillStyle = colourToString(getClosestColour(
    (Math.sin(((frame % 10000) / 10000) * (Math.PI * 2)) + 1) / 2,
    colourArr
  ));
  ctx.fillText(txt, (width - size.width) / 2, 360);

  drawBoxes(true);
});

document.body.addEventListener('click', () => console.log(dots));

app.start();
